import "./main-styles";
import "./layouts/layout-base";
import "./index.scss";

import "./components/input-field";
import "./components/button";
import DropdownDatepicker from "./components/dropdown-datepicker";
import CardSearch from "./components/card-search";

(function($){

  const className = "test1";

  new CardSearch($(`.js-${className}`), {
    form: {
      action: "/mock-address/change-me",
      method: "get"
    },
    title: "Найдём номера под ваши пожелания",
    dropdownDatepicker: {
      withTwoInputs: true,
      size: "big",
      datepicker: {
        isHidden: true
      },
      inputs: [
        {
          label: {
            title: "прибытие"
          },
          button: {
            icon: "expand_more"
          },
          isReadonly: true,
          type: "text",
          name: "startDate",
          size: "small"
        },
        {
          label: {
            title: "выезд"
          },
          button: {
            icon: "expand_more"
          },
          isReadonly: true,
          type: "text",
          name: "endDate",
          size: "small"
        }
      ]
    },
    dropdownGuests: {
      type: "guest",
      input: {
        label: {
          title: "гости"
        },
        value: "Сколько гостей",
        type: "text",
        name: "guests",
        button: {
          icon: "expand_more"
        }
      },
      items: [
        {
          title: "взрослые",
          value: 0,
          type: "guest"
        },
        {
          title: "дети",
          value: 0,
          type: "guest"
        },
        {
          title: "младенцы",
          value: 0,
          type: "baby"
        }
      ]
    },
    searchRoomButton: {
      text: "Подобрать номер",
      icon: "arrow_forward",
      theme: "long",
      type: "submit"
    }
  });

})(jQuery);